import React, { Component } from "react";
import "./App.css";
import CenteredGrid from "./components/centered-grid";
import Cart from "./components/cart";
import { BrowserRouter as Router, Switch } from "react-router-dom";
import PropsRoute from "./components/props-route";
import Checkout from "./components/checkout";

export default class App extends Component {
  state = {
    products: [] // get from localstorage later
  };

  handleBuy = ({ id, img, title, price }) => {
    this.setState(
      prevState => {
        const hasSameId = item => +item.id === +id;
        const hasNotSameId = item => +item.id !== +id;
        const productFound = prevState.products.find(hasSameId);

        if (productFound) {
          productFound.amount++;
          return {
            products: [...prevState.products.filter(hasNotSameId), productFound]
          };
        } else {
          return {
            products: [
              ...prevState.products,
              { id, img, title, price, amount: 1 }
            ]
          };
        }
      },
      () => console.log("save to localstorage")
    );
  };

  render() {
    const { products } = this.state;

    return (
      <Router>
        <Switch>
          <PropsRoute
            exact
            path="/"
            component={CenteredGrid}
            onProductClick={this.handleBuy}
          />
          <PropsRoute path="/cart" component={Cart} products={products} />
          <PropsRoute path="/checkout" component={Checkout} />
        </Switch>
      </Router>
    );
  }
}
